createFilterRequests() {
this.filterLocationRequest = [];
this.filterActivitiesRequest = [];
this.filterTimeRequst = [];
this.filterGymRequest = [];
this.filterBranchRequest = [];
this.filterFitnessLevelRequest=[];
this.filterpriceModelRequest=[];

for (let i = 0; i < this.selectedFilter.length; i++) {
   if (this.selectedFilter[i].type === 'place') {
   const val1 = {'id': this.selectedFilter[i].id, 'latitude': this.selectedFilter[i].lat, 'longitude': this.selectedFilter[i].lang};
   this.filterLocationRequest.push(val1);
  }
  if (this.selectedFilter[i].type === 'activity') {
    this.filterActivitiesRequest.push(this.selectedFilter[i].id_real)
  }
  if (this.selectedFilter[i].type === 'time') {
    this.filterTimeRequst.push({"fromTime":this.selectedFilter[i].fromTime,"toTime":this.selectedFilter[i].toTime})
  }
  if (this.selectedFilter[i].type === 'gym') {
    this.filterGymRequest.push(this.selectedFilter[i].id_real)
  }
  if (this.selectedFilter[i].type === 'branch') {
    this.filterBranchRequest.push(this.selectedFilter[i].id_real)
  }
  if (this.selectedFilter[i].type === 'fitnesslevel') {
    this.filterFitnessLevelRequest.push(this.selectedFilter[i].id_real)
  }
  if (this.selectedFilter[i].type === 'priceModel') {
    this.filterpriceModelRequest.push(this.selectedFilter[i].id_real)
  }
  }
      this.filterLocationLength = this.filterLocationRequest.length;
      this.filterActivitiesLength = this.filterActivitiesRequest.length;
      this.filterTimeLength = this.filterTimeRequst.length;
      this.filterGymLength  = this.filterGymRequest.length;
      this.filterBranchLength  = this.filterBranchRequest.length;

      this.filterFitnessLevelLength = this.filterFitnessLevelRequest.length;
      this.filterpriceModelLength = this.filterpriceModelRequest.length;

}



addRemoveFilters(event, val) {
  // this.param[val.mixpanel_type] = this.param.hasOwnProperty(val.mixpanel_type) ===  false ? [] : console.log('Exist')
  if (this.param.hasOwnProperty(val.mixpanel_type) ===  false) {
  this.param[val.mixpanel_type] = []
  }
  if (event) {
    this.selectedFilter.push(val);
    this.param[val.mixpanel_type].push(val.name);
  } else {
    this.selectedFilter = _.without(this.selectedFilter, _.findWhere(this.selectedFilter, {
      id_real: val.id_real,
      id: val.id,
      name: val.name,
      type: val.type
    }));
    this.param[val.mixpanel_type].splice(this.param[val.mixpanel_type].indexOf(val.name), 1)
    if (this.param[val.mixpanel_type].length === 0) {
        delete this.param[val.mixpanel_type]
    }

  }
  if (this.param && this.selectedFilter.length > 0) {
    this.mixpanel.logEventWithAnalytics(MixpanelEvent.filter, this.param);
  }
}
addRemoveFilterslocatio(event, val) {
  if (this.param.hasOwnProperty(val.mixpanel_type) ===  false) {
    this.param[val.mixpanel_type] = []
    }
  if (event) {
    this.selectedFilter.push(val);
    this.param[val.mixpanel_type].push(val.name);
  } else {
    this.selectedFilter.forEach(element => {
      const index: number = this.selectedFilter.indexOf(element);
        if (val.id === element.id) {
        this.selectedFilter.splice(index, 1);
        }
    });
    this.param[val.mixpanel_type].splice(this.param[val.mixpanel_type].indexOf(val.name), 1);
    if (this.param[val.mixpanel_type].length === 0) {
      delete this.param[val.mixpanel_type]
  }

  }
  if (this.param && this.selectedFilter.length > 0) {
    this.mixpanel.logEventWithAnalytics(MixpanelEvent.filter, this.param);
  }
}
