import { NgModule } from '@angular/core';

import { AppClassesList } from './classeslistview.component';

import { LoginRegisterModule } from '../loginRegister/loginRegister.module';
import { JoinClassPopupModule } from '../joinclasspopup/joinclasspopup.module';
import { routing } from './classlistview.routes';
import { FilterClassViewModule } from '../filterclassview/filterclassview.module';
import { CommonModule } from '@angular/common';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import { CoreModule } from '../core/core.module';
import { RegisterPartnerModule } from '../registerPartner/registerPartner.module';
import { SlickModule } from 'ngx-slick';
import { CoreModuleNew } from 'newcore/new-core.module';

@NgModule({
  imports: [
    LoginRegisterModule,
    JoinClassPopupModule,
    routing,
    FilterClassViewModule,
    CommonModule,
    InfiniteScrollModule,
    CoreModule,
    RegisterPartnerModule,
    SlickModule.forRoot(),
    CoreModuleNew
  ],
  declarations: [
    AppClassesList
  ],
  exports: [
    AppClassesList
  ]
})
export class ClassListViewModule { }



